@import "variables.scss";

.flexColumn {
	min-height: 80vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: $text-primary-color;
	text-align: left;
	padding: 10px;
	max-width: 45vw;
}

@media screen and (max-width: 768px) {
	.flexColumn {
		max-width: 100vw;
	}
}
